.hero {
  background: url("../../assets/shipment.jpg");
  background-size: cover; }
  @media screen and (max-width: 768px) {
    .hero .title {
      font-weight: bold;
      color: black; } }
  @media screen and (min-width: 769px), print {
    .hero .title {
      font-size: 2.5rem; } }
  @media screen and (min-width: 1000px) {
    .hero .title {
      font-size: 4rem;
      margin-top: 2rem; } }

